.container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    gap: 0px 30px;
    height: 100vh;
    justify-items: center;
    align-items: center;
}

.quote {
    font-weight: 500;
    height: 400px;
    width: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 3px solid black;
    background-color: rgba(200,200,200,1);
    text-align: center;
    padding: 0.5rem;
}

.options {
    font-weight: 500;
    height: 400px;
    width: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 3px solid black;
    background-color: rgba(200,200,200,1);
    text-align: center;
    padding: 0.5rem;
}

.answer {
    background-color: rgba(0,150,0,0.5);
}

.individualOptions {
    display: flex;
    justify-content: flex-end;
    padding: 0.2rem;
}
